.header {
  background: linear-gradient(to right, black, #7b0f17);
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.7);
}

.logoContainer {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.logo {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.siteName {
  font-size: 1.8rem;
  font-weight: 700;
  color: gold;
  user-select: none;
}
